<template>
  <div class="black-bg" v-if="state == true">
    <div class="white-bg">
        <img :src="원룸들[productNo].image" class="modal-img">
        <h4>{{원룸들[productNo].title}}</h4>
        <p>{{원룸들[productNo].content}}</p>
        <input v-model="month">
        <p> {{month}}개월 선택함</p>
        <p>{{원룸들[productNo].price * month}}</p>

        <button @click="$emit('closeModal')">닫기</button>
    </div>
  </div>
</template>

<script>
export default {
    name: 'TheModal',
    data(){
        return{
            month: 1,
        }
    },
    watch: {
        month(a){ // (a,b)a는 변경후, b는 변경 전
            if(a>=13){
                alert('13이상 입력하지 마시오 ')
                return this.month = 1;
            }else if(isNaN(a) == true) {
                alert('문자입력하지마라');
                this.month = 1;
            }
        }
    },
    props: {
        원룸들: Array,
        state: Boolean,
        productNo: Number,
    },
    updated() {
        if(this.month == 2) {
            alert('3개월부터 시작입니다');
            return this.month = '';
        }
    },
}
</script>

