<template>
  <transition name="fade">
    <Modal @closeModal="state = false" :원룸들="원룸들" :state="state" :productNo="productNo"/>
  </transition>

  <div class="menu">
    <a v-for="작명 in 메뉴들" :key="작명">{{작명}}</a> <!--<a v-for="작명 in 3" :key="">Home</a>-->
  </div>

  <Discount v-if="showDiscount == true" :discountNumber="discountNumber"/>

  <button @click="priceSort">가격순 정렬</button>
  <button @click="priceDes">가격 내림차순</button>
  <button @click="reset">정렬 리셋</button>
  <button @click="titleDes">가나다 순</button>

  <div v-for="(a,i) in 원룸들" :key="a">
    <Card @openModal="state = true; productNo = $event" :원룸="원룸들[i]"/>
  </div>
</template>


<script>

import data from './assets/oneroom.js';
import Discount from './Discount.vue';
import Modal from './Modal.vue';
import Card from './Card.vue';

export default {
  name: 'App',
  data(){
    return{
      // 데이터 보관함이라고 생각하기
      showDiscount: true,
      원룸들: data,
      state : false,
      count : [0,0,0],
      image : [require('./assets/room0.jpg'),require('./assets/room1.jpg'),require('./assets/room2.jpg')],
      메뉴들 : ['Home', 'Shop','About'],
      스타일 : 'color : blue',
      productNo: 0,
      discountNumber: 30,
    }
  },
  methods : {
    increase(){
      this.count += 1;
    },
    priceSort(){
      this.원룸들.sort(function(a,b){
        return a.price - b.price
      });
    },
    priceDes(){
      this.원룸들.sort(function(a,b){
        return b.price - a.price
      });
    },
    reset(){
      this.원룸들.sort(function(a,b){
        return a.id - b.id
      });
    },
    titleDes(){
      this.원룸들.title.sort(function(a,b){
         if (a > b) return -1;
        else if (b > a) return 1;
        else return 0;
      });
    },
  },

  mounted(){
    setInterval(()=>{
      if(this.discountNumber != 0){
        this.discountNumber--;
        }
    },2000);
  },

  components: {
    Discount: Discount,
    Modal: Modal,
    Card: Card,
  }
}
</script>

